@import '../../node_modules/swiper/swiper-bundle.css';
html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

input:focus, button:focus {
  outline: none;
}

@font-face {
  font-family: FuturaPT;
  src: url("../fonts/FuturaPT-Book.woff2") format("woff2"), url("../fonts/FuturaPT-Book.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: FuturaPT;
  src: url("../fonts/FuturaPT-Heavy.woff2") format("woff2"), url("../fonts/FuturaPT-Heavy.woff") format("woff");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: FuturaPT;
  src: url("../fonts/FuturaPT-Medium.woff2") format("woff2"), url("../fonts/FuturaPT-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: FuturaPT;
  src: url("../fonts/FuturaPT-Light.woff2") format("woff2"), url("../fonts/FuturaPT-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
body {
  margin: 0;
  font-family: "FuturaPT";
  font-weight: 400;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.wrapper {
  position: relative;
  overflow-x: hidden;
}

h2 span, h3 span {
  font-weight: 300;
}

.container {
  width: 1280px;
  margin: 0 auto 0 auto;
}

@media (max-width: 1300px) {
  .container {
    width: 1170px;
  }
}
@media (max-width: 1200px) {
  .container {
    width: 960px;
  }
}
@media (max-width: 992px) {
  .container {
    width: 720px;
  }
}
@media (max-width: 768px) {
  .container {
    width: 540px;
  }
}
@media (max-width: 576px) {
  .container {
    width: 100%;
    padding: 0 10px 0 10px;
  }
}
input, button, textarea {
  cursor: pointer;
  outline: none;
}

input:focus {
  border: 1px solid #095830;
}

.current-item, .page-catalog__subitem .current-item {
  color: #1A893A;
}

.has-submenu {
  position: relative;
}
.has-submenu::after {
  content: "";
  background: url("../img/sprite.svg#has-submenu");
  width: 8px;
  height: 8px;
  position: absolute;
  top: 50%;
  right: -16px;
  transition: 0.2s;
  margin-top: -4px;
}

.button {
  height: 69px;
  font-size: 24px;
  border: none;
  border-radius: 48px;
  transition: 0.2s;
}

.button-grad {
  color: #fff;
  background: linear-gradient(180deg, #1A893A 0%, #095830 100%);
  padding: 19px 51px;
  transition: 0.2s;
}

.button-o {
  background: #fff;
  border: 1px solid #1A893A;
  color: #1A893A;
  transition: 0.2s;
}

.button-more {
  padding: 19px 89px 19px 63px;
  position: relative;
  transition: 0.2s;
}

.button-more__icon {
  position: absolute;
  top: 50%;
  margin-top: -7px;
  margin-left: 11px;
  width: 15px;
  height: 15px;
  fill: #1A893A;
  transition: 0.2s;
}

.link-more {
  display: inline-block;
  position: relative;
  color: #1A893A;
  transition: 0.2s;
  font-size: 20px;
  font-weight: 500;
}
.link-more:before {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  background: #1A893A;
  transition: 0.2;
  bottom: 0;
}
.link-more svg {
  position: absolute;
  width: 10px;
  height: 10px;
  right: -21px;
  top: 50%;
  margin-top: -3px;
  transition: 0.2s;
  fill: #1A893A;
}

.open-submenu {
  width: 11px;
  height: 11px;
  background: transparent;
  border: none;
  margin-right: 10px;
}
.open-submenu svg {
  width: 8px;
  height: 8px;
  transform: rotate(180deg);
  transition: 0.2s;
}
.open-submenu_active svg {
  transform: rotate(0deg);
  transition: 0.2s;
}

@media (min-width: 992px) {
  .has-submenu:hover::after {
    transition: 0.2s;
    transform: rotate(180deg);
  }

  .button:hover {
    transition: 0.2s;
  }

  .button-o:hover {
    transition: 0.2s;
  }

  .button-more:hover {
    background: linear-gradient(180deg, #1A893A 0%, #095830 100%);
    color: #fff;
    transition: 0.2s;
  }
  .button-more:hover .button-more__icon {
    transition: 0.2s;
    fill: #fff;
  }

  textarea:focus {
    border: 1px solid #1A893A;
  }

  .link-more:hover {
    transition: 0.2;
  }
  .link-more:hover::before {
    transition: 0.2;
    width: 0;
  }
  .link-more:hover svg {
    right: -25px;
    transition: 0.2s;
  }

  .button {
    font-size: 18px;
  }
}
.header {
  position: relative;
}

.header-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-callback {
  position: absolute;
  right: 0;
  top: 0;
  height: 100px;
  background-color: #095830;
  color: #fff;
  width: 190px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.header-callback:before {
  position: absolute;
  content: "";
  width: 0px;
  height: 0px;
  border-left: 23px solid transparent;
  border-bottom: 100px solid #095830;
  left: -23px;
}

.header-logo {
  display: flex;
  align-items: center;
}
.header-logo img {
  margin-right: 14px;
}

.header__title {
  font-size: 30px;
  margin-bottom: 1px;
}

.header__subtitle {
  font-size: 18px;
  font-weight: 300;
}

.header-form {
  position: relative;
}

.header-form__input {
  width: 265px;
  height: 58px;
  border-radius: 40px;
  border: 1px solid #DCDCDC;
  padding: 20px 16px;
}

.header-form__submit {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #dedede;
  border: none;
  position: absolute;
  right: 5px;
  top: 5px;
}
.header-form__submit svg {
  width: 14px;
}

.header-socials__title {
  font-weight: 300;
  position: relative;
  display: block;
  margin-bottom: 8px;
  margin-left: 5px;
}
.header-socials__title::before {
  content: "";
  position: absolute;
  width: 3px;
  height: 3px;
  background: #095830;
  top: 50%;
  margin-top: -2px;
  left: -5px;
}

.header-buttons {
  display: flex;
}

.header-social__button {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  border: none;
  margin-right: 10px;
  background: #dedede;
  transition: 0.2s;
}
.header-social__button svg {
  width: 13px;
  height: 13px;
}
.header-social__button:last-child {
  margin-right: 0;
}

.header-social__whatsapp {
  fill: #1A893A;
}

.header-social__telegram {
  fill: #4089CC;
}

.header-social__envelope {
  fill: #095830;
}

.header-phones {
  display: flex;
}

.header-phones__icon {
  width: 44px;
  height: 44px;
  background: linear-gradient(180deg, #1A893A 0%, #095830 100%);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 15px;
}
.header-phones__icon svg {
  fill: #fff;
  width: 14px;
  height: 14px;
}

.header-numbers {
  display: flex;
  flex-direction: column;
}

.header-phones__number {
  font-weight: 500;
  font-size: 18px;
}

.header-contactus {
  color: #fff;
  background: #095830;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 190px;
  position: relative;
}
.header-contactus::before {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  border-bottom: 100px solid #095830;
  border-left: 23px solid transparent;
  left: -23px;
}

.header-contactus__title {
  font-size: 14px;
}

.header-contactus__button {
  display: block;
  font-weight: 800;
  font-size: 16px;
  position: relative;
  transition: 0.2s;
}
.header-contactus__button:before {
  content: "";
  position: absolute;
  transition: 0.2s;
  width: 100%;
  height: 1px;
  background: #fff;
  bottom: 0;
}

.navigation {
  padding-top: 15px;
  padding-bottom: 15px;
  border-top: 1px solid #dcdcdc;
}

.navigation-list {
  margin: 0;
  padding-left: 0;
  display: flex;
  justify-content: space-between;
  list-style: none;
}

.navigation-item {
  font-weight: 500;
  font-size: 18px;
  position: relative;
  transition: 0.2s;
}
.navigation-item::before {
  transition: 0.2s;
  content: "";
  position: absolute;
  width: 0%;
  height: 1px;
  background: #095830;
  bottom: 0;
}

.navigation-sublist {
  display: none;
  position: absolute;
  background: #fff;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  width: 271px;
  list-style: none;
  padding: 16px 14px 11px 14px;
  z-index: 1000;
}
.navigation-sublist .navigation-item {
  padding-bottom: 8px;
  border-bottom: 1px solid #dcdcdc;
  padding-left: 20px;
  padding-top: 8px;
}
.navigation-sublist .navigation-item:first-child {
  padding-top: 0;
}
.navigation-sublist .navigation-item:last-child {
  padding-bottom: 0;
  border-bottom: none;
}

.navigation-sublist .navigation-item::before {
  width: 0%;
  left: 0px;
}

.header-menu {
  display: none;
}

@media (min-width: 992px) {
  .header-social__button:hover {
    background: #095830;
    transition: 0.2s;
  }
  .header-social__button:hover svg {
    fill: #fff;
  }

  .header-phones__number {
    position: relative;
    transition: 0.2s;
  }
  .header-phones__number::before {
    position: absolute;
    content: "";
    width: 0%;
    height: 1px;
    bottom: -1px;
    background: #095830;
    transition: 0.2s;
  }
  .header-phones__number:hover {
    transition: 0.2s;
    color: #1A893A;
  }
  .header-phones__number:hover::before {
    transition: 0.2s;
    width: 100%;
  }

  .header-contactus__button:hover {
    transition: 0.2s;
    color: #1A893A;
  }
  .header-contactus__button:hover:before {
    transition: 0.2s;
    background: #1A893A;
  }

  .navigation-list .navigation-item:hover {
    transition: 0.2s;
  }
  .navigation-list .navigation-item:hover::before {
    transition: 0.2s;
    width: 100%;
  }
  .navigation-list .navigation-item:hover .navigation-sublist {
    display: block !important;
  }

  .navigation-sublist .navigation-item:hover::before {
    width: 100%;
    left: 0px;
  }

  .header-menu_adaptive {
    display: none;
  }
}
@media (max-width: 1200px) {
  .header {
    padding-top: 9px;
    padding-bottom: 9px;
  }

  .header__title {
    font-size: 28px;
  }

  .header-contactus {
    display: none;
  }
}
@media (max-width: 992px) {
  .header__titpxle {
    font-size: 24px;
  }

  .header__subtitle {
    font-size: 16px;
  }

  .header-socials {
    display: none;
  }

  .header-menu {
    display: block;
  }

  .header-menu__button {
    width: 35px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header-menu__button span {
    transition: 0.2s;
    width: 35px;
    height: 4px;
    background: #393939;
    border-radius: 3px;
    position: relative;
  }
  .header-menu__button span::before {
    transition: 0.2s;
    content: "";
    position: absolute;
    width: 35px;
    height: 4px;
    background: #393939;
    top: -9px;
  }
  .header-menu__button span::after {
    transition: 0.2s;
    content: "";
    position: absolute;
    width: 35px;
    height: 4px;
    background: #393939;
    bottom: -9px;
  }

  .header-menu__button_active span {
    transition: 0.2s;
    height: 0;
  }
  .header-menu__button_active span::before {
    transition: 0.2s;
    top: 0;
    transform: rotate(45deg);
  }
  .header-menu__button_active span::after {
    transition: 0.2s;
    top: 0;
    transform: rotate(-45deg);
  }

  .navigation {
    display: none;
  }
}
@media (max-width: 768px) {
  .header-numbers {
    display: none;
  }
}
@media (max-width: 576px) {
  .header-phones__icon {
    width: 36px;
    height: 36px;
  }
  .header-phones__icon svg {
    width: 12px;
    height: 12px;
  }

  .header__title {
    font-size: 14px;
  }

  .header-logo {
    width: 46px;
  }

  .header-phones {
    margin-left: auto;
    margin-right: 0;
  }
}
